import { Button, Modal, Table } from 'react-bootstrap'
import React from 'react'

const ModalComp = ({show,markerTokensData,closeModal}) => {
    return (
           <Modal show={show} onHide={closeModal} animation={false}
                size="lg"
                aria-labelledby="contained-modal-title-vcenter"
                centered
                
           >
                <Modal.Header closeButton >
                    <Modal.Title  >Tokens Details</Modal.Title>
                </Modal.Header>

                <Modal.Body>
                    <Table striped bordered hover variant="dark">
                        <thead>
                            <tr>
                                <th>#</th>
                                <th>Token Name</th>
                                <th>Token No</th>
                                <th>Token Id</th>
                            </tr>
                        </thead>
                        <tbody >
                        {markerTokensData.map((token,index)=>(
                            
                                <tr key={index}>
                                    <td >{token.id}</td>
                                    <td >{token.tokenName} </td>
                                    <td >{token.tokenNo}</td>
                                    <td >{token.tokenId}</td>
                                </tr>                        
                            
                         ))
                        }
                        </tbody>
                    </Table>
                </Modal.Body>

                <Modal.Footer>
                    <Button variant="primary" onClick={closeModal}>Close</Button>
                   
                </Modal.Footer>
            </Modal>
    )
}

export default ModalComp
