
import React, { useEffect } from 'react'
import { BrowserRouter as Router, Route,Switch } from 'react-router-dom'
import SignUp from './auth/registerForm/SignUp'
import Home from './Pages/Home'
import LoginForm from './auth/loginForm/LoginForm'
import { Layout } from './components/Layout/Layout'
import PrivateRoute from './components/PrivateRoute'
import './App.css'
import Dashboard from './Pages/Dashboard'
import { useDispatch, useSelector } from 'react-redux'
import { isLoggedInUser } from './Store/Actions/authActions'
import Footer from './components/Footer/Footer'
import ClientsDetails from './Pages/ClientsDetails'
import MarkerDetails from './components/Map/Marker/MarkerDetails'
import ContactUs from './Pages/ContactUs'

import Header from './components/headerLayout/Header'


function App() {
    const auth = useSelector(state => state.auth);
    const dispatch =useDispatch()
    
    useEffect(() =>{
        if (!auth.authenticated) {
            dispatch(isLoggedInUser())
        }
       
    },[])
   
    return (
        <>          
         <Router>
            <Header />
            <Layout>
                <Switch >
                    {/* <PrivateRoute path="/" exact component={Home} /> */}
                    {/* <Route path='/homepage' exact component={HomePage} /> */}
                    <Route path="/" exact component={Home} />
                    <Route path='/TokenApplication1/home' exact component={Home} />
                    <Route path='/TokenApplication1/home/:id' exact component={MarkerDetails} />
                    <Route path='/TokenApplication1/signup' exact component={SignUp} />
                    <Route path='/TokenApplication1/signin'exact component={LoginForm} />
                    <Route path='/TokenApplication1/markerDetail'exact component={ClientsDetails} />
                    <Route path='/TokenApplication1/contactUs' exact component={ContactUs} />
                </Switch>
            </Layout>
            <Footer />
       </Router>
      
    </>      
    )
}

export default App
