import React, { useEffect, useState,  } from 'react'
import { Card } from 'react-bootstrap'
import { useDispatch, useSelector } from 'react-redux'
import {  useFirestoreConnect } from 'react-redux-firebase'
import { useParams } from 'react-router-dom'
import { markersTokens, getTokens,getImages, } from '../../../Store/Actions/realtimeData'
import ModalComp from '../../ModalComponent/ModalComp'
import './markerDetails.css'


const MarkerDetails = (props) => {
    const {id} = useParams()
    
    const dispatch=useDispatch()
   
    const [show,setShow]=useState(false)

    const user = localStorage.getItem('user') ? JSON.parse(localStorage.getItem('user')) : null;
   
    const docData = useSelector(({firestore: {data} }) => data.clients && data.clients[id])
    
    const markerTokensData = useSelector(state => state.data.markerTokensData)
  
    const imageData = localStorage.getItem('imageUrl') ? JSON.parse(localStorage.getItem('imageUrl')) :null;

    var totalTokens=markerTokensData.map(ele=>ele.tokenNo)
    totalTokens.sort() 
    var lastTokenNo=totalTokens[totalTokens.length-1]

    console.log(imageData)
    useFirestoreConnect([
        { 
            collection:'clients',
            doc:id
        }
    ])
    const handleImageUrl=()=>{
        docData ? dispatch(getImages(docData.name)) : dispatch(getImages(''))
    }
    const removeImageData=()=>{
        localStorage.setItem('imageUrl',null);
    }

    useEffect(() => {
        dispatch(markersTokens(id)) 
        handleImageUrl()
        return ()=>{
            removeImageData();
        }
        
    }, [handleImageUrl()])

    function setToken(lastTokenNo){
       
        if(typeof lastTokenNo != "undefined" && lastTokenNo != null && lastTokenNo != null){
            dispatch(getTokens(id,user,lastTokenNo))
        }else{
            lastTokenNo=0
            
            dispatch(getTokens(id,user,lastTokenNo))
         } 
     }

    const generateTokens=()=>{
    
        const isDocPresent = markerTokensData.find((doc)=>
                                doc.tokenId===user.email          
                            )
        if(!isDocPresent){
            setToken(lastTokenNo)
        }else{
            alert(`The Token is already generated by You, check status for more detail on your token ` )
        }
    }

   /*  const statusButton=()=>{
                
        const isDocPresent = markerTokensData.find((doc)=>
            doc.tokenName===user.firstName          
        )
        isDocPresent ? (alert(`Your at ${isDocPresent.tokenNo} position` )) 
            : (alert("Please generate a token "))
    } */
  
    const showModal=()=> setShow(true)

    const closeModal=()=> setShow(false)

    return (
        <>
        <div className="container  "> 
                {
                    docData && 
                    <Card className="card-lg text-center card-center " bg="dark" key={docData.url} >
                        <div className="card-header bg-dark markerCardHeader">
                            {docData.name}
                        </div>
                        <div className="card-body markerCardBody">
                            
                            <div className="position-relative overflow-hidden">

                                <img className="img" src={`${imageData}`} height="300" width="400" alt="clientImage" />    
                            </div>
                            <h5 className="card-title title">{docData.address}</h5>
                            <p className="card-text text">
                                {docData.content}
                            </p>
                            
                            <div className="row customeRow align-items-center justify-content-around">
                                
                                <div className="col-auto ">
                                    <button className="btn btn-primary"
                                        onClick={generateTokens}    
                                    >Generate Tokens</button>
                                </div>
                                <div className="col-auto">
                                    {lastTokenNo ? <h2>No of tokens : {lastTokenNo}</h2>:<h2>You didn't have token</h2>}
                                </div>
                                <div className="col-auto">
                                    <button type="button" 
                                        className="btn btn-primary" 
                                        onClick={showModal}>
                                        Status
                                    </button>
                                    <div >
                                        <ModalComp show={show} closeModal={closeModal} markerTokensData={markerTokensData}  />
                                    </div>
                                   
                                                                        
                                </div>
                            </div>
                        </div>
                    </Card>
                }          
        </div>  
       </>
    )
}

export default MarkerDetails
