import React from 'react'
import { useDispatch, useSelector } from 'react-redux'
import { Link, NavLink } from 'react-router-dom'
import { logout } from '../../Store/Actions/authActions'
import './style.css'



const Header = () => {
  const auth = useSelector(state => state.auth)
  const dispatch = useDispatch();
  const user = localStorage.getItem('user') ? JSON.parse(localStorage.getItem('user')) : null;

  //console.log("user name",auth.firstName)

  const logOutButton=() =>{
   console.log("logout")
    dispatch(logout(auth.uid))
    localStorage.clear();
  }
  return (
    <nav className="navbar sticky-top navbar-expand-md navbar-dark bg-dark">
      <div className="container-fluid justify-content-lg-between "> 
        
          <div className="col-sm-2 ">
            <Link className="navbar-brand " to="/TokenApplication1/home">TokenApp</Link>
            <button className="navbar-toggler" 
                type="button" 
                data-bs-toggle="collapse" 
                data-bs-target="#navbarSupportedContent" 
                aria-controls="navbarSupportedContent" 
                aria-expanded="false" 
                aria-label="Toggle navigation">
              <span className="navbar-toggler-icon"></span>
            </button>
          </div>
          { !user ? 
              <div className="collapse navbar-collapse justify-content-md-center" id="navbarSupportedContent">
                <div className="navbar-nav">
                  <NavLink className="nav-link" activeClassName="active" exact to='/TokenApplication1/signin'>sign in</NavLink>
                  <NavLink className="nav-link" activeClassName="active" exact to='/TokenApplication1/signup'>Sign Up</NavLink>
                </div>
              </div> 
            :  
              <div className="container-fluid">
              <div className="collapse navbar-collapse justify-content-around " id="navbarSupportedContent">
                <div className="col-lg-6 ">
                <div className="navbar-nav justify-content-around ">

                  <NavLink className="nav-link" activeClassName="active" exact to='/TokenApplication1/home'>Home</NavLink>
                  
                  <NavLink className="nav-link" activeClassName="active" exact to='/TokenApplication1/markerDetail'>client Details</NavLink>
                
                  <NavLink className="nav-link" activeClassName="active" exact to='/TokenApplication1/contactUs'>contact us</NavLink>
                  
                </div>
                </div>

                <div className="col-auto ">
                  <div className="dropdown ">
                      <b className="navbar-brand dropdown-toggle"  id="navbarDropdown" role="button" data-bs-toggle="dropdown" aria-expanded="false">
                        <i className="fa fw fa-user fa-1x"></i>
                      </b>
                      <div className="dropdown-menu dropdown-menu-start dropdown-menu-dark" aria-labelledby="navbarDropdown">
                        <div className="card">
                          <li>{auth.firstName} {auth.lastName}</li>
                          <hr/>
                          <button className="btn btn-sm btn-danger text-uppercase" onClick={logOutButton } >logout</button>
                        </div>
                      </div>
                  </div>
                </div>
                
              </div>
             
              </div>
            }
      </div>
    </nav>
  )
}

export default Header
